// 引入 animate.css
import 'animate.css/animate.compat.css'
import { WOW } from 'wowjs'

// 将 WOW 添加到 Vue 的原型上，使其在所有组件中可用
Vue.prototype.$wow = new WOW({
  boxClass: 'wow',
  animateClass: 'animated',
  offset: 0,
  mobile: true,
  live: true
});

import VueTypedJs from 'vue-typed-js'

Vue.use(VueTypedJs)

new Vue({
  router,
  i18n,
  render: h => h(App),
  mounted() {
    this.$nextTick(() => {
      this.$wow.init();
    });
  }
}).$mount('#app')