            <template #item="{item, url, index}">
                <div class="card" id="cat-pic">
                    <LazyImg :url="url" class="card-img-top"/>
                    <div class="card-footer text-body-secondary text-center gochi-hand-regular">
                        {{ item.date }}
                    </div>
                </div>
            </template>
        </Waterfall>
    </div>
