在主屏幕上看到预订列表。房东会看到他们每个酒店的预订列表。客人看到他们所要求的预订列表(在许多房产上)。房东可以选择接受或拒绝临时预订，但不能接受同一酒店的重复预订。客人必须登录查看他们的临时预订是否被接受或拒绝。我们假设预订在其结束日期之前保持可见，然后自动删除。")]),_c('p',[_vm._v("对于每次预订，都可以推导出:住宿天数、每晚价格、服务费、保洁费和总费用(住宿费用、服务费和保洁费的总和)。一旦预订被主人接受，客人和主人将能够看到对方的保密联系信息。我们假定客人直接与主人联系以支付住宿费。客人还可以看到酒店的准确地址。所有保密资料均可由双方查阅，直至逗留结束")]),_c('p',[_vm._v("入住后，客人提交一份评论。每条评论都是一位客人针对一家酒店写的，虽然酒店可能会收到很多评论，客人也可能会收到很多评论。每条评论都包含了对客人满意程度的文字描述，并给出了1到5分的评分(从低到高)在六个方面得分:清洁、沟通(反应时间)、登记(容易程度)、准确性(公共信息)，位置(质量)和价值(金钱)。对于每个属性，都可以推导出在所有提交的评论中，每个类别的平均分数，以及总体平均评分。一个如果房东的房产平均评分保持在4.7分，那么他们就成为了超级房东。")])])])
